.home {
    grid-row: content-s/content-e;
    grid-column: s/e;
    margin-top: 38px;
    padding-bottom: 59px;

    &__items {
        &__subnav {
            display: grid;
            justify-items: center;
            

            &__link {
                display: grid;
                grid-template-columns: [whatsnew-s whatsnew-e] 1fr [industry-s industry-e] 1fr [smarterchroma-s smarterchroma-e] 1fr [brand-s brand-e] 1fr [lcmode-s lcmode-e] 1fr [news-s news-e] 1fr;
                justify-items: center;

                margin: 20px;
            }
        }

        &__filter {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            grid-gap: 10px;

            .dropdown {
                display: flex;
                justify-content: space-between;

                .dropdown-label {
                    padding: 5px;
                    font-weight: 600;
                }

                .dropdown-toggle {
                    width: 200px;
                }

            }

            &__heading {
                padding: 80px;
            }

            &__head-label {
                display: grid;
                justify-content: center;
                margin: 30px;
            }
        }
    }

}